'use strict';

var audio = document.querySelector('.ring__song');

var db = void 0,
    input = void 0,
    ul = void 0;

function databaseOpen(callback) {
  var req = window.indexedDB.open('alarmDB', 1);

  req.onupgradeneeded = function (e) {
    db = e.target.result;
    e.target.transaction.onerror = databaseError;
    db.createObjectStore('alarm', { keyPath: 'timeStamp' });
  };

  req.onsuccess = function (e) {
    db = e.target.result;
    callback();
  };
  req.onerror = databaseError;
}

var alarmList = [];

function alarmsFound() {
  input = document.querySelector('.modal__form_input');
  ul = document.querySelector('.content__alarm');
  document.body.addEventListener('submit', onSubmit);
  databaseAlarmsGet(renderAllAlarms);

  databaseAlarmsGet(function (alarm) {
    for (var i = 0; i < alarm.length; i++) {
      alarmList.push(parseInt(alarm[i].alarmTime.replace(':', '')));
    }
  });
}

databaseOpen(alarmsFound);

var currentTime = void 0;

function checkTime() {
  var time = new Date();

  currentTime = (time.getHours() < 10 ? '0' : '') + time.getHours() + (time.getMinutes() < 10 ? '0' : '') + time.getMinutes();

  currentTime = parseInt(currentTime);
}

function checkAlarm() {
  if (alarmList.indexOf(currentTime) >= 0) {
    (function () {
      var ring = document.querySelector('.ring');
      ring.style.visibility = 'visible';
      audio.play();

      ring.addEventListener('click', function () {
        ring.style.visibility = 'hidden';
        audio.pause();
      });
    })();
  }
}

setInterval(function () {
  checkTime();
  checkAlarm();
}, 60 * 1000);

function renderAllAlarms(alarms) {
  var html = '';
  alarms.forEach(function (alarm) {
    html += alarmToHtml(alarm);
  });
  ul.innerHTML = html;
}

function alarmToHtml(alarm) {
  return '<li class="content__alarm_elem">' + '<span>' + alarm.alarmTime + '</span>' + '<button>&times;</button>' + '</li>';
}

function databaseAlarmsGet(callback) {
  var transaction = db.transaction(['alarm'], 'readonly');
  var store = transaction.objectStore('alarm');

  var keyRange = window.IDBKeyRange.lowerBound(0);
  var cursorRequest = store.openCursor(keyRange);

  var data = [];
  cursorRequest.onsuccess = function (e) {
    var result = e.target.result;

    if (result) {
      data.push(result.value);
      result.continue();
    } else {
      callback(data);
    }
  };
}

function databaseError(e) {
  console.error('An IndexedDB error has occurred', e);
}

function onSubmit(e) {
  e.preventDefault();
  databaseAlarmsAdd(input.value, function () {
    databaseAlarmsGet(renderAllAlarms);
    input.value = '';
    alarmsFound();
  });
}

function databaseAlarmsAdd(alarmTime, callback) {
  var transaction = db.transaction(['alarm'], 'readwrite');
  var store = transaction.objectStore('alarm');
  var req = store.put({
    alarmTime: alarmTime,
    timeStamp: Date.now()
  });

  transaction.oncomplete = function (e) {
    callback();
  };
  req.onerror = databaseError;
}
'use strict';

var modal = document.querySelector('.modal');
var openModal = document.querySelector('.content__button');
var setButton = document.querySelector('.modal__form_button');
var closeModal = document.querySelector('.modal__overlay_close-button');

openModal.addEventListener('click', function () {
  modal.style.visibility = 'visible';
});

setButton.addEventListener('click', function () {
  modal.style.visibility = 'hidden';
});

closeModal.addEventListener('click', function () {
  modal.style.visibility = 'hidden';
});
'use strict';

function updateTime() {
  var time = new Date();
  var timeSelector = document.querySelector('.content__time');

  timeSelector.innerHTML = (time.getHours() < 10 ? '0' : '') + time.getHours() + ':' + (time.getMinutes() < 10 ? '0' : '') + time.getMinutes();
}

setInterval(updateTime, 1000);